import React from 'react'
import {Button} from '@material-ui/core'
import './Poster.css'
function Poster() {
    return (
        <div className="poster">
            <div className="poster__info">
            <span className="poster__heading">The<br/> Gratest Outdoors</span>
            <span className="poster__description">Wishlists curated by Airbnb</span>
            <Button className="button" var="primary">Get Inspired</Button>
        </div>
        </div>
    )
}

export default Poster
