import React from 'react'
import './Banner.css'
import SearchIcon from "@material-ui/icons/Search";
import LanguageIcon from "@material-ui/icons/Language";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { Avatar, Button } from "@material-ui/core";

function Banner() {
    return (
        
        <div className="banner">
        <div className='header'>
         
               <a href="https://www.airbnb.co.in/?landing_page_section=HEADER"><img
                    className="header__icon"
                    src="https://i.pinimg.com/originals/3c/bf/be/3cbfbe148597341fa56f2f87ade90956.png"
                    alt=""/></a>
           
           
           

            <div className='header__right'>
            <Button variant="outlined">Become Host</Button>
            <LanguageIcon className="language__icon" />
                <ExpandMoreIcon />
                <Avatar />
            </div>
        </div>
    <div className="banner__content">
           <div className="banner__info">
               <h2>Get out and stretch your imagination</h2>
                <h5>
                    Plan a different kind of getaway to uncover the hidden gems near you.
                </h5>
 
                <Button variant="outlined">Explore more</Button>

        </div>

        <div className='header__center'>
            <div className='header__center__box'>

            <div className='searchbox'>
                <div class="searchbox__info">
                   <div className='searchbox__heading'>
                  Place
                  </div>
                  <input className="input" type="text" placeholder="where are you going?"></input>
                </div>
                </div>

             <div className="divider">|</div>

             <div className='searchbox'>
                <div class="searchbox__info">
                   <div className='searchbox__heading'>
                   Checkin
                  </div>
                  <input className="input" type="number" placeholder="add dates"></input>
                </div>
                </div>

             <div className="divider">|</div>

             <div className='searchbox'>
                <div class="searchbox__info">
                   <div className='searchbox__heading'>
                   Checkout
                  </div>
                  <input className="input" type="number" placeholder="add dates"></input>
                </div>
                </div>

             <div className="divider">|</div>

               <div className='searchbox'>
               <div class="searchbox__info">
                   <div className='searchbox__heading'>
                   Guests
                  </div>
                  <input className="input" type="number" placeholder="add guests"></input>
                </div>
                <SearchIcon className="search__icon" />
                </div>

               
                </div>


            </div>
            </div>
    </div>
        
    )
    }

export default Banner
