import React from 'react'
import './CardMini.css'

function CardsMini({image,location,duration}) {
    return (
        <div className="cardmini">
            <div className="cardmini__pic">
            <img src={image} alt=""/>
         </div>

            <span className="cardmini__info">
                <span className="location"> {location}</span> 
                <span className="duration"> {duration}</span>
            </span>


        </div>
    )
}

export default CardsMini
