import React from 'react'
import './CardMedium.css'

function CardMedium({image,type}) {
    return (
        <div className="cardmedium" >
            <span className="cardmedium__pic">
                <img src={image} alt=""/>    
            </span> 

            <span className="cardmedium__info">
                <span >{type}</span>
            </span>

        </div>
    )
}

export default CardMedium
